.App{
  height: 100vh;
  padding-top: 0px;
  background-color: #fff;
  font-family: Arial, Helvetica, sans-serif;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  flex-direction: column;
  align-items: center;
}
.logo-contenedor{
  width: 100%;
  height: 50px;
  margin-top: 0;
  margin-bottom: 10px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.logo{
  height: 50px;
}
.contenedor-calculadora
{
  width: 400px;
  height: 600px;
  padding: 14px;
  background-color: #99c9ff;
  border-radius: 20px;
  border:5px solid black;
}
.fila{
  margin: 4px;
  display: flex;
  align-items: center;
  justify-content: center;
  
}