.boton-contenedor{
    height: 83px;
    display: flex;
    flex: 1 1;
    justify-content: center;
    align-items: center;
    font-weight: bold;
    font-size: 38px;
    background-color: #1b1b32;
    color:#fff;
    border-radius:25px;
    border: 2px solid white;
    margin:4px;
    cursor:pointer;
    user-select: none;
}
.operador{
    background-color: #00471b;
}
.boton-contenedor:hover{
    background-color: aqua;
}